var ListItem, uuid;

uuid = require("uuid");

ListItem = (function() {
  class ListItem {
    constructor(model) {
      var that;
      that = this;
      // if passed an object, assign it to the current item
      if (model != null) {
        this.depouchify(model);
      }
    }

    // update mirrored items
    refresh() {}

    // refresh?

    // attributes starting with _ are managed and need to be updated every time a change has been made

    // array version of list items
    default_value_for(key) {
      var output;
      output = null;
      switch (key) {
        case "uuid":
          output = uuid();
          break;
        case "is_expanded":
        case "is_expanded_radar":
          output = true;
          break;
        case "is_ongoing":
        case "is_hidden":
          output = false;
          break;
        case "progress":
          output = {
            percent_complete: 0,
            color: "light-gray"
          };
          break;
        case "index":
        case "model_version":
          output = 0;
          break;
        case "list_items":
        case "labels":
        case "ancestry":
          output = [];
      }
      return output;
    }

    
    // POUCH Conversions

    depouchify(model) {
      var that;
      //console.log "depouchify list_item #{model.name}"
      //console.log model
      that = this;
      //pouchDocument = {}

      //model = pouchDocument

      // merge into this instance
      //console.log Object.keys(model)
      return that.attribute_whitelist.forEach(function(key) {
        // check that model has key
        if (Object.has(model, key)) {
          switch (key) {
            case "list_items":
              that.list_items = [];
              model.list_items.sortBy("index");
              return model.list_items.forEach(function(item, index) {
                item = new ListItem(item);
                item.index = index; // fix index values
                return that.list_items.append(item);
              });
            default:
              return that[key] = model[key];
          }
        } else {
          return that[key] = that.default_value_for(key);
        }
      });
    }

    pouchify_list_items() {
      var output;
      output = [];
      this.list_items.forEach(function(listItem) {
        return output.append(listItem.pouchify());
      });
      return output;
    }

    pouchify() {
      // de-class this instance for pouch
      // map the root attributes
      // iterate through the list_items
      return this.saveable_model();
    }

    saveable_model() {
      var output;
      output = Object.select(this, this.attribute_whitelist);
      output.list_items = this.pouchify_list_items;
      return output;
    }

    rekeyChildren() {
      var newAncestry;
      // console.error "rekeyChildren #{@name}"
      // console.log "---ancestry"
      // console.log @
      // console.log @ancestry
      newAncestry = this.ancestry.clone().append(this.uuid);
      return this.list_items.forEach(function(item) {
        item.ancestry = newAncestry;
        item.uuid = uuid();
        return item.rekeyChildren();
      });
    }

    clone() {
      var cloneObject;
      cloneObject = new ListItem(this);
      console.log("--- ListItem.clone");
      console.log(cloneObject);
      cloneObject.name = `${cloneObject.name} - clone ${Date.create().format('{HH}:{mm}:{ss}')}`;
      cloneObject.uuid = uuid();
      cloneObject.rekeyChildren();
      return cloneObject;
    }

    model() {
      return {
        // if sourceversion < latest version, then some upgrades may be required
        // if null then return latest
        uuid: "",
        model_version: 0,
        // common to root items
        name: "new list item",
        description: "", // optional, not visible, edit in a modal
        progress: {
          percent_complete: 0,
          color: "light-gray"
        },
        index: 0, // integer # ordinal
        ancestry: [],
        is_hidden: false,
        is_expanded: false, // collapse state
        is_ongoing: false,
        due_date: null,
        list_items: [],
        labels: [],
        
        // /common
        has_clip: false,
        clip_value: "",
        user_defined_data: {}
      };
    }

    
    // levels are set by children attribute

    // LIST ITEM MANAGEMENT
    add_list_item(model, index) { // should be a class already
      // if index is null, add to end

      //model.ancestry = @ancestry.clone().append(@uuid)
      model.index = this.list_items.length;
      // go through the model's children to handle ancestry and index
      model.fix_ancestry(this);
      console.error(`add_list_item ${index}`);
      if (index != null) {
        this.list_items.splice(index, 0, model);
      } else {
        this.list_items.append(model);
      }
      //console.debug JSON.stringify(@list_items,null, 2)
      return this.reindex(); // for good measure
    }

    //console.debug JSON.stringify(@list_items,null, 2)
    fix_ancestry(parent) {
      var that;
      that = this;
      // change ancestry
      // ancestry should be parent's ancestry plus parent uuid
      this.ancestry = parent.ancestry.clone().append(parent.uuid);
      // needs to recurse down the chain
      console.log("fix_ancestry");
      console.log(this);
      return this.list_items.forEach(function(listItem) {
        return listItem.fix_ancestry(that);
      });
    }

    reindex() {
      return this.list_items.forEach(function(listItem, index) {
        return listItem.index = index;
      });
    }

    remove_list_item(listItem) { // doesn't matter if it's a class, we only need the uuid
      this.list_items.remove({
        uuid: listItem.uuid
      });
      return this.reindex();
    }

    // reindex

    // methods
    current_level() {
      return int;
    }

    is_sublist() {
      return boolean;
    }

    sublists() {
      return [];
    }

    move(v) {} // can only move to same parent

  };

  ListItem.prototype.model = "List Item";

  ListItem.prototype.attribute_whitelist = [
    "uuid",
    "model_version",
    "name",
    "description",
    "progress",
    "index",
    "ancestry",
    "is_hidden",
    "is_expanded",
    "is_expanded_radar",
    "is_ongoing",
    "is_obfuscated", // if the item is used as a clip
    "due_date",
    "list_items",
    "labels",
    "user_defined_data"
  ];

  return ListItem;

}).call(this);

module.exports = ListItem;
