<script lang="coffeescript">

  import { onMount } from 'svelte'

  import Router from 'svelte-spa-router'

  import { isSyncing, dueToday } from "./helpers/stores.js"
  import Pouch from "./helpers/pouch_db.js"

  # Common

  import FourOFour from './components/FourOFour.svelte'
  import LeftSplit from './components/Layout/LeftSplit.svelte'
  import Toast from "./components/Toast/Toast.svelte"
  import FavIconBadge from "./components/FavIconBadge/FavIconBadge.svelte"


  # Local


  import SideNavigator from './pages/_Common/SideNavigator.svelte'

  import Radar from './pages/Radar/Index.svelte'
  import Projects from './pages/Lists/Index.svelte'

  import Project from './pages/Lists/Show.svelte'


  import Task from './pages/Sublists/Show.svelte'

  import Settings from './pages/Settings/Settings.svelte'
  import Sync from './pages/Sync/Sync.svelte'
  import Help from './pages/Help/Index.svelte'


  showToast = false
  toastMessage = ""

  routes =

    '/': Radar
    '/lists': Projects
    '/lists/:id': Project
    '/lists/:project_id/tasks/:id': Task
    '/lists/:project_id/documents/:id': Document
    '/lists': Projects



    '/settings': Settings
    '/help': Help
    '/sync': Sync
    '*': FourOFour


  appName = "Braintapper Radar"


  window.createPouchDb = ()->
    window.db = new PouchDB('braintapper-radar', {auto_compaction: true})
    window.db.createIndex
      index:
        fields: ["type"]
    window.pouch = new Pouch window.db

  window.createPouchDb()

  window.mounted = ()->
    window.pouch.sync_store = isSyncing
    window.pouch.notifier = toast
    window.pouch.sync()

  toast = (message)->
    showToast = true
    toastMessage = message

  syncCheck = ()->
    doSync = true
    if window.pouch.sync_db
      lastSyncAttempt = localStorage.getItem("radar_last_sync_attempt")
      if lastSyncAttempt?
          minutesSinceLastSync = Date.create().minutesSince(lastSyncAttempt)
          doSync = (minutesSinceLastSync >= 5)
      unless window.pouch.is_syncing
        if doSync
          window.pouch.sync()



  syncCheck.every(60000) # minute


  onMount ()->
    window.mounted()

  `$: show = showToast`
  console.log "Braintapper Application Build 3"

  routeEvent = (e)-> #  router must be mounted or won't work
    console.error "routeEvent"
    console.debug e

  toaster = (e)->

</script>


<svelte:head>
  <title>{ appName }</title>
</svelte:head>



<LeftSplit headerSize="40px" contentScrollable={true} on:toast={toaster}>
  <div slot="nav">
    <SideNavigator on:toast={toaster}/>
  </div>
  <div slot="default" style="padding: 48px 96px">
    <Router {routes} on:routeEvent={routeEvent}/>
  </div>
</LeftSplit>
<Toast bind:show={showToast} message={toastMessage} delay={2}/>



