<script lang="coffeescript">


  import { Row, RowCell } from "../../../../components/Flex"
  import Menu from '../../../../components/Overlay/Menu/ComposedMenu.svelte'
  import Indent from "../Indent.svelte"

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export index = undefined
  export item = undefined
  export indent = 0
  export listId = undefined

  export sameListOnly = false
  export showMenu = true
  export dragState = undefined


  indentButtonRef = undefined

  draggableEl = undefined
  draggable = false

  isOver = false
  isInvalid = false

  checkTypesForListId = (types)->
    types.find (t)->
      return t.startsWith("listid:")

  start = (e)->
    item["listId"] = listId
    item["index"] = index || null
    e.dataTransfer.setData("text/plain", JSON.stringify(item))
    e.dataTransfer.setData("listid:#{listId}", JSON.stringify(item))
    dispatch "dragging", true

  end = (e)-> # stop dragging
    dispatch "dragStop", true
  over = (e)->
    e.preventDefault()
    isOver = true
    unless !sameListOnly
      draggedListId = checkTypesForListId(e.dataTransfer.types)
      if draggedListId?
        isInvalid = !(listId == draggedListId.replace("listid:",""))
      else
        isInvalid = true

  leave = (e)->
    
    draggable = false
    isOver = false
    isInvalid = false

  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false
    e.preventDefault()
    dispatch "dragStop", true
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))
    dropped = true
    unless !sameListOnly
      # console.log "This list: #{listId} :: itemList #{dragItem.listId}"
      dropped = (listId == dragItem.listId)
    if dropped
      dispatch "nest",
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: item
        dropIndex: index
        dropListId: listId



  mouseup = ()->

    draggable = false
    isInvalid = false
    

  mousedown = ()->
    draggable = true

  getWidth = (el)->
    if el?
      el.clientWidth - 48
    else
      0


  toggleExpanded = ()->
    console.log "toggle Expanded"
    item.is_expanded = !item.is_expanded
    dispatch "update", {uuid: item.uuid, is_expanded: item.is_expanded, ancestry: item.ancestry }

  `$: draggableWidth = getWidth(draggableEl) `

</script>


<div
  item-draggable
  data-id={item.id}
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragover={over}
  on:dragstart={start}
  on:dragend={end}
  on:dragleave={leave}
  bind:this={draggableEl}
  layout="row"
>
      <div flex="initial" handle on:mouseup={mouseup(item)} on:mousedown={mousedown(item)} show-on-hover>
        <slot name="grabber">
          <svg class="handle" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M9,3H11V5H9V3M13,3H15V5H13V3M9,7H11V9H9V7M13,7H15V9H13V7M9,11H11V13H9V11M13,11H15V13H13V11M9,15H11V17H9V15M13,15H15V17H13V15M9,19H11V21H9V19M13,19H15V21H13V19Z"/></svg>
        </slot>
      </div>

      <div flex class="slot">
          <div layout="row" item-interior>
          <div flex="initial" style="width: {indent * 24}px">
            <Indent level={indent}/>
          </div>
          
          <div flex="initial" collapsible>
          {#if item.list_items.length > 0}
            <button icon on:click={toggleExpanded}>
              {#if item.is_expanded}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path class="fill" d="M7 10l5 5 5-5H7z"/></svg>
              {:else}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path class="fill" d="M10 17l5-5-5-5v10z"/></svg>
              {/if}
            </button>
          {/if}
          </div>
          <div flex>
            <slot item={item} index={index}/>
          </div>
        </div>
      </div>
      <div flex="initial" menu draggable-item-menu show-on-hover>
        <slot name="menu"/>
      </div>

</div>
<style global lang="sass">
  // global to ensure child menus inherit styles
  [item-draggable]

    // border: 1px solid red //debug
    border-radius: 3px

    // width & position
    width: calc(100% + 64px)
    max-width: calc(100% + 64px)
    margin-left: -32px // 24+8px for icon cols


    [item-interior]
      width: 100%
      max-width: 100%

    [handle], [menu] // set cell wdiths
      width: 32px


    [menu]
      text-align: center

    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%


    [show-on-hover]
      svg
        path
          fill: transparent
      &[is-showing="true"]
        svg
          path
            fill: var(--blue) !important
      
    



    [collapsible]
      width: 24px
      svg, svg.menu
        display: inline




    [handle]:hover
      cursor: grab

    &.over
      background: #dddddd
    &.invalid
      background: var(--dark-red) !important
    &:hover
      background: #fcfcfc
      [handle], [collapsible], [draggable-item-menu]
        svg, svg.menu
          display: inline


      [show-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)

      [handle]
        svg
          path
            fill: var(--light-gray)


      [collapsible]
        button[icon]
          padding: 0px !important
        svg
          path.fill
            fill: var(--light-gray)
        &:hover
          svg
            path
              fill: var(--blue)



</style>
