// requires global variable db, declared in App.svelte
// each item is a document in pouchdb
var ListItem, RootItem;

ListItem = require("./list_item.js");

RootItem = (function() {
  class RootItem {
    // attributes starting with _ are managed and need to be updated every time a change has been made

    // array version of list items

    // this is not a function because of Svelte

    // convert list_items from an object to an array -- from pouch
    default_value_for(key) {
      var output;
      output = null;
      switch (key) {
        case "id":
        case "_id":
          output = uuid();
          break;
        case "is_expanded":
        case "is_expanded_radar":
          output = true;
          break;
        case "is_ongoing":
        case "is_archived":
        case "is_favorite":
          output = false;
          break;
        case "progress":
          output = {
            percent_complete: 0,
            color: "light-gray"
          };
          break;
        case "index":
        case "model_version":
          output = 0;
          break;
        case "list_items":
        case "labels":
        case "ancestry":
          output = [];
          break;
        case "settings":
          output = {
            // importable from other projects
            is_expanded: false,
            user_defined_data: [{}],
            // importable from other projects
            labels: [
              {
                value: "unique",
                foreground: "",
                background: "",
                icon: "" // structure tbd
              }
            ]
          };
      }
      //...
      return output;
    }

    constructor(model) {
      var that;
      that = this;
      // if passed an object, assign it to the current item
      //console.error "NEW ROOT ITEM CONSTRUCT"
      //console.log model
      if (model != null) {
        model["type"] = "RootItem"; // force it
        this.depouchify(model);
      }
    }

    override(model) {}

    // update mirrored items
    refresh() {}

    // @list_items = @@list_items
    stub(override) {
      var base, newId;
      base = this.model();
      newId = uuid();
      base._id = newId;
      base.id = newId;
      base.name = `New ${this.type} created at ${Date.create().format('%Y-%m-%d %H:%M:%S')}`;
      base.created_at = Date.create();
      base.updated_at = Date.create();
      if (override != null) {
        return Object.merge(base, override, true);
      } else {
        return base;
      }
    }

    /*

    RADAR

    */
    /*

    LIST ITEM HANDLING

    */
    reindex() {
      return this.list_items.forEach(function(listItem, index) {
        //console.log "listItem #{index}"
        //console.log listItem
        return listItem.index = index;
      });
    }

    add_list_item_to_root(item, scb, ecb, fcb) {
      var newRecord, that;
      // add a list item to the current
      that = this;
      console.log("addListItem");
      //console.log "item"
      //console.log item
      newRecord = new ListItem(item);
      //console.log newRecord
      //console.log newRecord.uuid
      newRecord.index = this.list_items.length;
      this.list_items.append(newRecord);
      // @refresh()
      return this.update(scb);
    }

    add_list_item(model, index, scb, ecb, fcb) { // should be a class already
      // if index is null, add to end
      if (model.model == null) {
        model = new ListItem(model);
      }
      //model.ancestry = @ancestry.clone().append(@uuid)
      model.index = this.list_items.length;
      // go through the model's children to handle ancestry and index
      model.fix_ancestry({
        ancestry: []
      });
      console.error(`add_list_item ${index}`);
      console.log(model);
      if (index != null) {
        this.list_items.splice(index, 0, model);
      } else {
        console.log("@list_items.append");
        this.list_items.append(model);
        console.debug(this.list_items);
      }
      //console.debug JSON.stringify(@list_items,null, 2)
      this.reindex(); // for good measure
      console.debug(this.list_items);
      //console.debug JSON.stringify(@list_items,null, 2)
      if (scb != null) {
        return scb();
      }
    }

    fix_ancestry(parent) {
      var that;
      that = this;
      // change ancestry
      // ancestry should be parent's ancestry plus parent uuid
      this.ancestry = [];
      // needs to recurse down the chain
      console.log("fix_ancestry");
      console.log(this);
      return this.list_items.forEach(function(listItem) {
        return listItem.fix_ancestry(that);
      });
    }

    delete_list_item(listItem, scb) {
      var runningElement;
      // console.error "DELETE LIST ITEM"
      // console.log listItem
      if (listItem.ancestry.length === 0) {
        // console.log "root level removal"
        this.list_items.remove({
          uuid: listItem.uuid
        });
      } else {
        // console.log "child level removal"
        // console.log listItem
        // console.log @list_items
        runningElement = this;
        listItem.ancestry.forEach(function(key) {
          var index;
          index = runningElement.list_items.findIndex({
            uuid: key
          });
          return runningElement = runningElement.list_items[index];
        });
        // console.log "runningElement"
        // console.log runningElement
        runningElement.list_items.remove({
          uuid: listItem.uuid
        });
      }
      if (scb != null) {
        return scb();
      }
    }

    // get the item containing the list_items
    reference_from_ancestry(ancestry) {
      var outputReference;
      outputReference = this; // starts at this root_item's list_items
      if (ancestry.length > 0) {
        ancestry.forEach(function(uuid, index) {
          var levelIndex;
          console.log(`nest level ${index} uuid: ${uuid}`);
          if (outputReference != null) {
            levelIndex = outputReference.list_items.findIndex({
              uuid: uuid
            });
            return outputReference = outputReference.list_items[levelIndex];
          } else {
            return console.error(`outputReference ${uuid} not found, ancestry seems broken/invalid`);
          }
        });
      }
      //console.log outputReference
      //console.log "outputReference"
      //console.log outputReference
      return outputReference;
    }

    // click on divider
    insert_item(item, destination, scb, ecb) {
      var destinationListReference, droppedItem, that;
      console.error("insert item");
      that = this;
      // the passed item from the event is not a ListItem instance
      droppedItem = new ListItem(item);
      // note
      // todo
      // to handle deletes, maybe prefix original uuid with an underscore before further handling, or use a new uuid on moved item?

      // nest the item in the appropriate location
      destinationListReference = that.reference_from_ancestry(destination.ancestry);
      destinationListReference.add_list_item(droppedItem, destination.index + 1);
      if (scb != null) {
        console.log("--scb");
        return scb(null);
      }
    }

    nest_item(origin, destination, scb, ecb) {
      var destinationItem, droppedItem, indexOverride, invalidDrop, isRootDrop, isRootOrigin, isSameLevel, nest, originReference, that, toIndex;
      console.error("nest item");
      that = this;
      invalidDrop = false;
      if (destination.ancestry.includes(origin.uuid)) {
        console.error("CAN'T DROP IN DESCENDANT");
        invalidDrop = true;
      }
      // drag on parent
      console.log("NEST PARENT CHECK");
      console.log(`destination uuid: ${destination.uuid}`);
      console.log(`origin.ancestry.last(): ${origin.ancestry.last()}`);
      if (destination.uuid === origin.ancestry.last()) {
        console.log("CAN'T REDROP ON PARENT");
        invalidDrop = true;
      }
      if (destination.uuid === origin.uuid) {
        invalidDrop = true;
      }
      // the passed item from the event is not a ListItem instance
      droppedItem = new ListItem(Object.clone(origin));
      destinationItem = Object.clone(destination.saveable_model());
      console.log("nest_item destinationItem");
      console.log(destinationItem);
      toIndex = null;
      if (Object.has(destination, "to_index")) {
        toIndex = destination.to_index;
      }
      indexOverride = false; // drop on divider, splice it into the array and fix the indices
      isSameLevel = false; // no need to nest if on same level, is just a reorder
      
      // check if dropping to root
      isRootOrigin = origin.ancestry.length === 0; // if true delete from @list_items, else drill down
      isRootDrop = destination.ancestry.length === 0;
      // validity checks

      // downstream drop
      if (destination.ancestry.includes(origin.uuid)) {
        console.error("CAN'T DROP IN DESCENDANT");
        invalidDrop = true;
      }
      // drag on parent
      if (destination.uuid === origin.ancestry.last) {
        console.log("CAN'T REDROP ON PARENT");
        invalidDrop = true;
      }
      if (destination.uuid === origin.uuid) {
        invalidDrop = true;
      }
      originReference = that.reference_from_ancestry(origin.ancestry);
      // note
      // todo
      // to handle deletes, maybe prefix original uuid with an underscore before further handling, or use a new uuid on moved item?
      nest = function(index) {
        var destinationListReference;
        console.log("root_item.nest");
        destinationListReference = that.reference_from_ancestry(destination.ancestry.clone().append(destinationItem.uuid));
        destinationListReference.add_list_item(droppedItem, index);
        return originReference.list_items.remove({
          uuid: origin.uuid
        });
      };
      if (!invalidDrop) {
        nest(toIndex);
        console.log(this.list_items);
        if (scb != null) {
          console.log("--scb");
          return scb(null);
        }
      }
    }

    // handle root item
    archive(includeChildren) {}

    // includeChildren = false by default
    // set is_archived = true
    // handle unarchived children by
    unarchive(includeChildren) {}

    // includeChildren = false by default
    // set is_archived = false
    // parent_id is null
    // children should be archived, handle

    // divider drop event
    drop_item(origin, destination, scb, ecb) {
      var destinationReference, invalidDrop, itemIndex, moveOrigin, originReference, that;
      console.error("---------------------------------------------------------------------------------------------------------------------------------");
      console.error("RootItem.drop_item");
      console.error("---------------------------------------------------------------------------------------------------------------------------------");
      console.log("origin");
      console.debug(origin);
      console.log("destination");
      console.debug(destination);
      that = this;
      invalidDrop = false;
      originReference = that.reference_from_ancestry(origin.ancestry);
      destinationReference = that.reference_from_ancestry(destination.ancestry);
      moveOrigin = true;
      if (origin.ancestry.isEqual(destination.ancestry)) {
        console.error("SAME ANCESTRY - DO NOT DELETE ORIGIN");
        moveOrigin = false;
      }
      // reorder items from target forward
      destinationReference.list_items.forEach(function(listItem, index) {
        if (index >= origin.index) {
          return listItem.index = listItem.index + 1;
        }
      });
      if (moveOrigin) { // move the item
        origin.index = destination.index;
        if (origin.model == null) {
          origin = new ListItem(origin);
        }
        destinationReference.add_list_item(origin, destination.index + 1); // + 1 puts it after the item
      } else {
        // just reindex
        // note assumes that items are correctly indexed

        // clean indices and push down items
        destinationReference.list_items.forEach(function(listItem, index) {
          if (index > destination.index) {
            
            // console.log "pushing list item #{listItem.name}"
            return listItem.index = index + 1;
          } else {
            return listItem.index = index;
          }
        });
        itemIndex = destinationReference.list_items.findIndex({
          uuid: origin.uuid
        });
        //console.log "original index: #{destinationReference.list_items[itemIndex].index }"
        //console.log "drop index: #{destination.index + 1}"
        //console.error "BEFORE"
        //console.table destinationReference.list_items.clone(), null, 2
        destinationReference.list_items[itemIndex].index = destination.index + 1;
      }
      //console.error "AFTER"
      //console.table destinationReference.list_items.clone(), null, 2

      // return 
      if (moveOrigin) {
        // delete old item
        originReference.list_items.remove({
          uuid: origin.uuid
        });
      }
      destinationReference.list_items.sortBy("index");
      console.error("after sorted");
      console.log(destinationReference.list_items);
      originReference.reindex();
      destinationReference.reindex();
      console.error("after sort and reindex");
      console.debug(destinationReference.list_items);
      if (scb != null) {
        console.log("--scb");
        scb(null);
      }
    }

    after_construct() {}

    sync() {
      console.log("pouchresourece.sync");
      return window.pouch.sync();
    }

    //-----------------------------------------------

    // db

    //-----------------------------------------------
    saveable_model() {
      var output;
      output = Object.select(this, this.attribute_whitelist);
      output.list_items = this.pouchify_list_items;
      return output;
    }

    depouchify(model) {
      var that;
      // get the document, map the attributes to this class instance
      // recurse through the list_items attributes and convert
      // the list_items to ListItem classes
      that = this;
      return this.attribute_whitelist.forEach(function(key) {
        // check that model has key
        if (Object.has(model, key)) {
          switch (key) {
            case "list_items":
              that.list_items = [];
              model.list_items.sortBy("index");
              return model.list_items.forEach(function(item, index) {
                item = new ListItem(item); // note this will trigger recursive depouchify on child items
                item.index = index; // fix index values
                return that.list_items.append(item);
              });
            default:
              return that[key] = model[key];
          }
        } else {
          return that[key] = that.default_value_for(key);
        }
      });
    }

    pouchify_list_items() {
      var output;
      output = [];
      this.list_items.forEach(function(listItem) {
        return output.append(listItem.pouchify());
      });
      return output;
    }

    pouchify() {
      var that, writeable;
      // de-class this instance for pouch
      // map the root attributes
      // iterate through the list_items and use the saveable model
      that = this;
      writeable = {};
      that.attribute_whitelist.forEach(function(key) {
        // console.warn "key: #{key} value: #{that[key]}"
        return writeable[key] = that[key];
      });
      // todo: fill in any gaps that might be missing here

      //console.error "pouchify writeable"
      //console.debug writeable
      return writeable;
    }

    cloneListItem(item, thenCb) {
      var clonedItem, itemToClone, parentReference, that;
      // get the original item reference
      // duplicate it
      // assign it a new uuid and index from its parent
      // rekey
      that = this;
      parentReference = that.reference_from_ancestry(item.ancestry);
      itemToClone = parentReference.list_items.find({
        uuid: item.uuid
      });
      console.log("itemToClone");
      console.debug(itemToClone);
      clonedItem = itemToClone.clone();
      console.error("clonedItem");
      console.debug(clonedItem);
      parentReference.add_list_item(clonedItem);
      if (thenCb != null) {
        return thenCb();
      }
    }

    clone(thenCb) {
      var cloneItem, that;
      that = this;
      cloneItem = new RootItem(this);
      cloneItem.id = uuid();
      cloneItem.name = `${this.name} - clone ${Date.create().format('{HH}:{mm}:{ss}')}`;
      cloneItem.list_items.forEach(function(item) {
        item.uuid = uuid();
        return item.rekeyChildren();
      });
      return db.find({
        selector: {
          type: "RootItem",
          is_archived: cloneItem.is_archived
        }
      }).then(function(result) {
        var writeRecord;
        // set the index depending on whether it's current or archived
        cloneItem.index = result.docs.length;
        // save the item
        cloneItem.created_at = Date.create();
        // todo: guid consistency check first
        // console.log "NER RECORD"
        writeRecord = cloneItem.pouchify();
        // delete writeRecord.id
        // bdelete writeRecord._id
        writeRecord["_id"] = writeRecord.id;
        console.debug(writeRecord);
        return db.put(writeRecord).then(function(response) {
          // note the response is this format:
          // ok: true
          // id: id
          // rev: rev_id
          if (thenCb != null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (typeof catchCb !== "undefined" && catchCb !== null) {
            return catchCb(err);
          } else {
            console.log("insert.catch");
            return console.log(err);
          }
        }).finally(function() {
          console.log("insert finally");
          that.sync();
          if (typeof finallyCb !== "undefined" && finallyCb !== null) {
            console.log("insert.finally");
            return finallyCb();
          }
        });
      }).catch(function(err) {
        if (typeof catchCb !== "undefined" && catchCb !== null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (typeof finallyCb !== "undefined" && finallyCb !== null) {
          return finallyCb();
        }
      });
    }

    get(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.get(id).then(function(doc) {
        console.error("get");
        console.table(doc);
        that.depouchify(doc);
        if (thenCb != null) {
          // consider removing transformation
          return thenCb(that);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("RootItem.get");
          return console.debug(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    update(thenCb, catchCb, finallyCb) {
      var changedDoc, that;
      that = this;
      changedDoc = this.pouchify();
      return db.get(changedDoc.id).then(function(originalDoc) {
        changedDoc["_id"] = changedDoc.id;
        changedDoc["_rev"] = originalDoc._rev;
        // Object.merge
        changedDoc.updated_at = Date.create();
        return db.put(changedDoc).then(function(response) {
          console.log("--------------root item updated");
          if (thenCb != null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (catchCb != null) {
            return catchCb(err);
          }
        });
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("RootItem.update");
          return console.debug(err);
        }
      }).finally(function() {
        window.pouch.sync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    destroy(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.find({
        selector: {
          "id": id
        }
      }).then(function(response) {
        var refreshedCb, removedCb;
        if (thenCb != null) {
          refreshedCb = function() { // execute then cb
            return thenCb(that.items);
          };
          removedCb = function() { // refresh @ items
            that.sync();
            // that.select refreshedCb, catchCb, finallyCb
            if (thenCb) {
              return thenCb();
            }
          };
          console.log("response");
          console.log(response);
          // docToRemove = response.docs.first()
          response.docs.forEach(function(doc) {
            return doc["_deleted"] = true;
          });
          
          // db.remove docToRemove, removedCb
          return db.bulkDocs(response.docs).then(removedCb).catch(function(err) {
            if (catchCb != null) {
              return catchCb(err);
            } else {
              console.error("RootItem.destroy");
              return console.debug(err);
            }
          });
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("RootItem.destroy");
          return console.debug(err);
        }
      }).finally(function() {
        that.sync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

  };

  RootItem.prototype.attribute_whitelist = [
    "_id", // this is the more reliable - used by couch/pouch
    "id",
    "model_version",
    "type",
    "name",
    "description",
    "progress",
    "index",
    "level",
    "is_favorite", // can include archived
    "is_expanded",
    "is_expanded_radar",
    "is_archived",
    "due_date",
    "list_items",
    "labels",
    "settings"
  ];

  return RootItem;

}).call(this);

module.exports = RootItem;
