<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  export is_hidden = undefined
  export has_link = false
  export due_date = false
  export is_ongoing = false
  export context = "list" # default

  select = (action)->
    dispatch("selected",action)


</script>
<ul role="menu">
  {#if has_link}
  <li><button type="button" on:click={select("openLink")}>Open Link</button></li>
  <li><button type="button" on:click={select("copyLink")}>Copy Link</button></li>
  <hr/>
  {/if}
  {#if due_date}
  <li><button type="button" on:click={select("openGoogleCalendarDay")}>Open Google Calendar</button></li>
  <li><button type="button" on:click={select("createGoogleCalendarEvent")}>Create a Google Calendar Event</button></li>
  <hr/>
  {/if}
  {#if context != "radar"}  
  <li><button type="button" on:click={select("addChild")} disabled>Nest New Item</button></li>
  <li><button type="button" on:click={select("addBelow")} disabled>Insert New Item Below</button></li>
  <hr/>
  {/if}  
  <li><button type="button" on:click={select("toggleOngoing")}>{ is_ongoing ? "Unset" : "Set"} as Ongoing</button></li>  
  <hr/>
  <li><button type="button" on:click={select("copy")}>Copy Text to Clipboard</button></li>
  
  <li><button type="button" on:click={select("hide")}>
    {#if is_hidden}
    Unhide
    {:else}  
    Hide
    {/if} Text
  </button></li>
  {#if context != "radar"}
  <hr/>
  <li>
    <button type="button" on:click={select("clone")}>Clone</button>
  </li>  
  <li>
    <button type="button" on:click={select("delete")}>Delete</button>
  </li>
  {/if}
</ul>
