<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()


  import tippy from 'tippy.js'


  import Menu from './_ListItem.svelte'
  import DeleteConfirmation from '../../../components/Overlay/Modal/DeleteConfirmation.svelte'

  export item = undefined
  export context = undefined

  showDeleteConfirmation = false



  import Toast from "../../../components/Toast/Toast.svelte"
  message = ""
  showToast = false


  


  tippyObject = undefined
  component = undefined

  copy = ()->
    console.log "copy clicked"
    dummy = document.createElement("input")
    document.body.appendChild(dummy)
    dummy.setAttribute("id", "clip-#{item.uuid}")
    document.getElementById("clip-#{item.uuid}").value=item.name
    dummy.select()
    document.execCommand("copy")
    document.body.removeChild(dummy)
    # toast it!
    message = "Copied value for item named <b>#{item.name.truncateOnWord(255)}</b> to clipboard"
    showToast = true


  getFirstLink = ()->
    words = (item.name || "").words()
    return words.find (word)->
      return word.startsWith("http://") || word.startsWith("https://") || word.startsWith("file://") || word.startsWith("mailto://")


  openLink = ()->
    foundLink = getFirstLink()
    if foundLink?
      window.open(getFirstLink(), '_blank')
    else
      message = "No link found"
      showToast = true

  copyLink = ()->
    console.log "copy clicked"
    dummy = document.createElement("input")
    document.body.appendChild(dummy)
    dummy.setAttribute("id", "clip-#{item.uuid}")

    foundLink = getFirstLink()
    if foundLink?
      document.getElementById("clip-#{item.uuid}").value=getFirstLink()

      dummy.select()
      document.execCommand("copy")
      document.body.removeChild(dummy)
      # toast it!
      message = "Copied value for link: <b>#{getFirstLink().truncateOnWord(255)}</b> to clipboard"
    else
      message = "No link found"
    showToast = true


  handleMenuSelection = (selection)->
    switch selection
      when "delete"
        showDeleteConfirmation = true
      when "copy"
        copy()
      when "openLink"
        openLink()

      when "copyLink"
        copyLink()

      when "clone"
        console.log "List Item Menu"
        console.log "clone"
        dispatch "clone", { uuid: item.uuid, ancestry: item.ancestry }
      when "addBelow"
        console.error "addbelow"

      when "addChild"
        console.error "addchild"
      when "toggleOngoing"
        dispatch "update", { uuid: item.uuid, ancestry: item.ancestry, is_ongoing: !item.is_ongoing}

      when "hide"

        console.error "hide"
        dispatch "update", { uuid: item.uuid, ancestry: item.ancestry, is_hidden: !item.is_hidden}



      when "openGoogleCalendarDay"
        #  
        # 
        calendarUrl = Date.create(item.due_date).format("https://calendar.google.com/calendar/u/0/r/day/{yyyy}/{M}/{d}")
        console.log calendarUrl
        window.open calendarUrl, '_blank'


      when "createGoogleCalendarEvent"

        eventName = item.name.escapeURL()
        #console.log "eventName #{eventName}"
        startDate = Date.create(item.due_date).format("{yyyy}{MM}{dd}")
        #console.log startDate
        endDate = Date.create(item.due_date).addDays(1).format("{yyyy}{MM}{dd}")
        #console.log endDate
        calendarUrl = "https://calendar.google.com/calendar/r/eventedit?text=#{eventName}&dates=#{startDate}/#{endDate}"

        # https://calendar.google.com/calendar/u/0/r/eventedit?text=My+Custom+Event&dates=20180512/20180513
        #https://calendar.google.com/calendar/r/eventEdit?text=Royale+VelourUltraPlush&dates=20201006/20201007
        console.log calendarUrl
        window.open calendarUrl, '_blank'

        # https://calendar.google.com/calendar/r/eventedit?text=My+Custom+Event&dates=20180512T230000Z/20180513T030000Z&details=For+details,+link+here:+https://example.com/tickets-43251101208
      else
        console.log "do nothing"
      # 





  popover = (element, content) ->

    tippyOptions =
      theme: "menu"
      appendTo: 'parent'
      trigger: "click"
      placement: "bottom-end"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$set 
          is_hidden: item.is_hidden
          is_ongoing: item.is_ongoing
          has_link: getFirstLink()?
          due_date: item.due_date
          context: context

        component.$on "selected",  (e) ->
          tippyObject.hide()
          handleMenuSelection e.detail
        return

    else
      tippyOptions.content = content
    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        console.error "update"
        console.debug params
        #if component
        #  component.$set { is_hidden: "yas" }

        return
      destroy: ->
        tippyObject.destroy()
        if component
          # debugger
          component.$destroy()
        return

    }



  deleteConfirmationCancel = ()->
    showDeleteConfirmation = false

  destroy = ()->
    dispatch "destroy", item
    showDeleteConfirmation = false


  updated = (item)->
    # console.error "--------------------------------------updatedD"
    if component?
      component.$set
        is_hidden: item.is_hidden
        is_ongoing: item.is_ongoing
        has_link: getFirstLink()?
        due_date: item.due_date
        context: context

  `$: updated(item)`

  

</script>

<button type="button" icon use:popover={Menu}>
  <slot>
    <svg class="menu" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M12,16A2,2 0 0,1 14,18A2,2 0 0,1 12,20A2,2 0 0,1 10,18A2,2 0 0,1 12,16M12,10A2,2 0 0,1 14,12A2,2 0 0,1 12,14A2,2 0 0,1 10,12A2,2 0 0,1 12,10M12,4A2,2 0 0,1 14,6A2,2 0 0,1 12,8A2,2 0 0,1 10,6A2,2 0 0,1 12,4Z"/></svg>
  </slot>
</button>
<Toast bind:show={showToast} {message}/>
{#if showDeleteConfirmation}
  <DeleteConfirmation on:destroy={destroy} on:cancel={deleteConfirmationCancel}/>
{/if}
