<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  import { slide } from "svelte/transition"
  import { sineIn, sineOut } from 'svelte/easing'
  import QuickEntry from "../../../_Common/QuickEntry/GenericItem.svelte"
  dispatch = createEventDispatcher()

  import Indent from "../Indent.svelte"


  export index = undefined
  export indentation = 0
  export item = { is_divider: true }
  export listId = undefined
  export firstItem = false

  export delay = 250 # click
  export dragState = false

  isOver = false
  isHover = false
  isInvalid = false

  export isExpanded = false

  over = (e)->
    e.preventDefault()
    e.stopPropagation()
    isOver = true

    isInvalid = false

    # console.error "OVER"

    #if isInvalid
    #  return e.preventDefault()
    #console.log "return is invalid: #{!isInvalid}"
    # return !isInvalid

  hover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      isHover = true
      # console.error "HOVER"

  unhover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      # console.log "UNHOVER"
      isHover = false
      collapse()

  leave = (e)->
    draggable = false
    isOver = false
    isInvalid = false
    # console.error "LEAVE"
    # isHover = false

  add = (e)->
    if e?
      e.stopPropagation()
    toIndex = item.index
    if firstItem
      if index # takes priority
        toIndex = index
      else
        toIndex = 0

    isHover = false
    isExpanded = false
    console.error "divider add"
    console.debug { ancestry: item.ancestry, index: toIndex }
    dispatch "add", { ancestry: item.ancestry, index: toIndex }

  toggleExpanded = ()->
    isExpanded = !isExpanded

  collapse = (e)->
    # console.log "collapse"
    if e?
      e.stopPropagation()
    isExpanded = false


  # Click tracking

  clickCount = 0

  clickReset = ()->
    switch clickCount
      when 1
        single()
      when 2
        double()
      else
        clickCount = 0
    # }
  delayedReset = clickReset.debounce(delay)



  single = () ->
    clickCount = 0
    toggleExpanded()
    # collapse.delay(5000)
    

    #dispatch "clicked", id

  double = () ->
    clickCount = 0
    add()
    #dispatch "doubleclicked", id

  click = (e)->
    e.stopPropagation
    clickCount++
    delayedReset()



  ###

  Drop Cases

  1. Drop to Reorder

  2. Drop to Nest and Reorder


  If target ancestry is the same as origin, then it''s a reorder 

  If target ancestry is different from origin, then it''s a nested move

  ###

  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false


    eventType = "drop"
  

    ancestry = item.ancestry.clone()

    # ancestry.pop()
    
    console.log "NestableDivider.drop item"
    console.debug item
    dropItem = { ancestry: ancestry, index: item.index }

    console.log "dropItem"
    console.log dropItem


    #console.log "NestableDivider.svelte"

    e.preventDefault()
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))

    #console.log item

    #console.log "--------------------------------"

    unless firstItem
      if item.list_items.length > 0
        console.log "------------------------------ DIVIDER DROP"
        console.log "dragitem"
        console.debug dragItem
        console.log "dropitem"
        console.log item


        console.log "item.list_items.length #{item.list_items.length} :: nest"
        eventType = "drop"
        item["to_index"] = 0
      else

        console.log "DROP ITEM #{item.name} index: #{item.index}"
        item["to_index"] = item.index + 1

      # console.log "evntType: #{eventType}"

      dropped = true

      unless Object.has(item, "is_divider")
        item["is_divider"] = true

      #console.log "DroppableDivider - drop"
      #console.log item

    else # handle first item
      console.error "FRIST ITEM"
      item["to_index"] = 0

      console.log dragItem
      console.log "eventType #{eventType}"
      dropped = true

    if dropped
      dispatch "drop",
        divider: true
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: dropItem # item
        dropIndex: index + 1
        dropListId: listId




</script>

<div
  divider
  layout="row"
  class:over={isOver}
  class:hover={isHover}
  is-dragging={dragState}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
  on:mouseenter={hover}
  on:mouseleave={unhover}
  class:expanded={isExpanded ? "expanded" : false}
  index={item.index}
>
  <div spacer flex="initial"/>
  <Indent level={indentation} flex="initial"/>
  <div flex
    class:expanded={isExpanded ? "expanded" : false}
    class:over={isOver}
    on:click={click}
    class="indented"
  >
    
    {#if isExpanded}
      <div class="add-divider"
        in:slide="{{delay: 150, duration: 300, easing: sineIn }}"
        class:invalid={isInvalid}
         
         layout="row"
      >
        <div>
          
        </div>
        <button layout="row" on:click={add}>
          <div flex="initial" icon><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M11,16V13H21V11H11V8L7,12L11,16M3,20H5V4H3V20Z"/></svg></div>
          <div flex>Click to Add New List Item Here</div>          
        </button>
        <button layout="row" on:click={collapse} cancel>
          <div flex="initial" icon><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M12,2A10,10 0 0,1 22,12A10,10 0 0,1 12,22A10,10 0 0,1 2,12A10,10 0 0,1 12,2M12,4A8,8 0 0,0 4,12C4,13.85 4.63,15.55 5.68,16.91L16.91,5.68C15.55,4.63 13.85,4 12,4M12,20A8,8 0 0,0 20,12C20,10.15 19.37,8.45 18.32,7.09L7.09,18.32C8.45,19.37 10.15,20 12,20Z"/></svg></div>
          <div flex>Cancel</div>                  
        </button>

      </div>
    {/if}
    
      <div class="droptarget"
        class:invalid={isInvalid}>
          
          {#if dragState}
            <div layout="row">
            <div flex="initial"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M11,16V13H21V11H11V8L7,12L11,16M3,20H5V4H3V20Z"/></svg></div>
            <div flex>Drop to Move List Item Here</div>
            </div>
          {/if}
          &nbsp;
      </div>
    



  </div>
</div>


<style lang="sass">



  div[divider]
    // margin: 2px 0px
    height: 8px
    transition: height 0.15s ease-out
    border-radius: 4px

    .indented
      border-radius: 4px
      background: transparent


    &:hover
      .indented
        background: #f1f1f1
    
    //&.over
    //  height: 24px
    //  transition: height 0.15s ease-in
    //  .indented
    //    background: #f1f1f1
    
    &.expanded
      height: 40px
      transition: height 0.15s ease-in


    .expanded
      padding: 8px 0px
      border-top: 8px solid transparent
      border-bottom: 8px solid transparent

      height: 40px

      .indented
        background: transparent
    //background: var(--lightest-blue)


    // drag and drop
    .droptarget
      height: 100%
      color: transparent
      line-height: 24px

    .indented.over
      .droptarget
        padding: 0px 8px
        color: var(--light-gray) !important
        background: transparent
        border-radius: 4px

    .indented.hover



    .add-divider
      text-align: left
      border-radius: 3px
      border: 0px
      padding: 0px 8px
      line-height: 24px
      top: -6px
      vertical-align: middle
      button
        line-height: 24px
        margin-right: 8px
      [icon]
        padding-right: 8px
        svg
          fill: var(--green)
        color: var(--green)
      button[cancel]
        margin-left: 18px     
        svg
          fill: var(--red)
        color: var(--red)           
      // background: var(--green) !important
    // hover state


    &[is-dragging="true"] //.dragging
      //height: 24px
      //transition: height 0.15s ease-in      
      //margin: 2px 0px
      
      //.indented
      //  background: #f1f1f1      
      height: 24px
      transition: height 0.15s ease-in
      margin-bottom: 2px
      .indented
        background: #f1f1f1
      .droptarget
        padding: 0px 8px 
        color: var(--light-gray) !important
        background: transparent
        border-radius: 4px
        svg
          path
            fill: var(--light-gray)
      &.over
        .indented
          background: var(--gray)
          color: white



    &.invalid
      .placeholder
        background: var(--red) !important




    div[spacer] // accommodate expand collapse icon
      width: 24px

</style>
