// list
var RootItem, RootItemList;

RootItem = require("./root_item.js");

RootItemList = (function() {
  class RootItemList {
    constructor() {}

    /*

    RADAR

    */

    // UI RELATED OPERATIONS

    getDragGroup(item) {
      var baseLevel, dragGroup, draggedIndex, draggedItem, endLoop;
      draggedItem = item;
      draggedIndex = item.index;
      dragGroup = [item];
      endLoop = false;
      baseLevel = 0;
      console.error("getDraggedGroup");
      console.log("item");
      console.log(item);
      console.log(`draggedIndex ${draggedIndex}`);
      this.items.forEach(function(i, index) {
        if (index > draggedIndex) { // do not include the dragged item
          if (i.level <= draggedItem.level) {
            endLoop = true;
          }
          if (!endLoop) {
            return dragGroup.append(i);
          }
        }
      });
      dragGroup.forEach(function(i, index) {
        if (index === 0) {
          baseLevel = i.level;
        }
        return i["base_level"] = i.level - baseLevel;
      });
      return dragGroup;
    }

    // adjust the indents of the dragged items based on their new location
    shiftDragIndents(dragGroup, startLevel) {
      var offset;
      console.error("shiftDragIndents");
      offset = dragGroup[0].level - startLevel;
      // base_level is created in dragGroup fn
      //console.log "@items[changeIndex].base_level:  #{@items[changeIndex].base_level}, startLevel: #{startLevel}"
      //@items[changeIndex].level = (@items[changeIndex].base_level ) + startLevel
      return dragGroup.forEach(function(item) {
        item.level = item.base_level + startLevel;
        return console.log(item);
      });
    }

    // return dragGroup

    // adjust the indices affected by a move/nest
    shiftDropIndices(startIndex, length) {
      console.error(`shiftDropIndices, startIndex: ${startIndex}, length: ${length}`);
      return this.items.forEach(function(i, index) {
        if (index > startIndex) {
          return i.index = i.index + length;
        }
      });
    }

    // update the indices of the drag group
    shiftDragIndices(dragGroup, startIndex) {
      return dragGroup.forEach(function(i, index) {
        return i.index = startIndex + index;
      });
    }

    archive() {}

    unarchive() {}

    // Note that this is a flat list that does not use nested objects for hierarchy
    // Hierarchy is managed by
    // parent_id
    // level
    // index (for ordering)
    drop(event, successCallback, errorCallback, finallyCallback) {
      var changeIndex, dragGroup, dragIndex, dragItem, dropIndex, dropItem, droppedOnDivider, move, nest, startLevel, startShift, targetItem, that;
      //console.log "Root Item List"
      //console.log "drop event"
      //console.log event
      console.error("ROOT ITEM LIST DROP");
      that = this;
      droppedOnDivider = event.divider || false;
      dropIndex = event.dropIndex;
      dragIndex = event.dragIndex;
      dragItem = event.dragItem;
      dropItem = event.dropItem; // ignore if droppedOnDivider, as the data is bad
      move = false;
      nest = false;
      if (dropIndex === dragIndex) { // same
        console.error("Same location, don't do anything");
      } else if ((dropIndex === 0) || (dropIndex === this.items.length)) { // top / bottom
        //console.log "dragIndex (#{dragIndex}) == (dropIndex - 1)  #{@items.length - 1} ::  #{dragIndex == (@items.length - 1)}"
        //console.log "(dropIndex (#{dropIndex})  == (@items.length - 1) :: #{dropIndex == (@items.length - 1)}"
        if ((dragIndex === (this.items.length - 1)) && (dropIndex === this.items.length)) { // last item to last item
          console.error("Already last item, don't do anything");
        } else {
          move = true;
        }
      } else {
        move = true;
      }
      //todo: actualDropIndex (put item at end of parent on a drop)
      if (move) {
        console.log("move drag group");
        dragGroup = this.getDragGroup(dragItem);
        console.log(dragGroup);
        targetItem = this.items[dropIndex];
        if (droppedOnDivider) {
          // check the item above it
          if ((dropIndex === 0) || (dropIndex === (this.items.length - 1))) { // top item is shifted to root level
            if (dropIndex === (this.items.length - 1)) {
              console.log("DROPPED ON LAST");
            }
            startLevel = 0; // check the item above
          } else {
            startShift = dropIndex;
            startLevel = targetItem.level; // same as item above
          }
        } else {
          if (dropIndex === 0) { // top item is level 1
            startLevel = 1;
          } else {
            startLevel = targetItem.level + 1; // same as item above
          }
        }
        nest = startLevel > 0;
        changeIndex = this.items.findIndex({
          id: dragGroup[0].id
        });
        if (nest) {
          console.log("nest");
          console.log(targetItem);
          if (targetItem.id != null) {
            this.items[changeIndex].parent_id = targetItem.id;
          } else {
            this.items[changeIndex].parent_id = null;
          }
          console.log(`@items[changeIndex].parent_id: ${this.items[changeIndex].parent_id}`);
        } else {
          console.log("don't nest");
          this.items[changeIndex].parent_id = null;
        }
        this.shiftDragIndents(dragGroup, startLevel);
        // shift ordinals below drop
        this.shiftDropIndices(dropIndex, dragGroup.length);
        this.shiftDragIndices(dragGroup, dropIndex);
        // update the dragGroup in the main list
        dragGroup.forEach(function(item) {
          var foundIndex;
          foundIndex = that.items.findIndex({
            id: item.id
          });
          return that.items[foundIndex] = item;
        });
        // pouchdb can't do a bulk patch style update, need to pass all fields

        // todo: transform@items to savable attributes only
        return db.bulkDocs(this.items).then(function(response) {
          var refreshedCb;
          //console.log "bulk change success"
          //console.log "reponse"
          //console.log response
          refreshedCb = function() {
            return successCallback(that.items);
          };
          return that.select(refreshedCb, errorCallback, finallyCallback);
        }).catch(function(err) {
          if (errorCallback != null) {
            return errorCallback(err);
          } else {
            console.log("put.catch");
            return console.log(err);
          }
        }).finally(function() {
          that.sync();
          if (finallyCallback != null) {
            return finallyCallback();
          }
        });
      }
    }

    //console.log "shifted draggroup"
    //console.log dragGroup

    //else # need to check level of item above

    // check is valid

    // drag and drop handling goes here

    // OBJECT MANAGEMENT

    transformCollection(items) {
      var that;
      that = this;
      items = items.map(function(i) {
        return new RootItem(i);
      });
      //console.error "transformed collection"
      //console.table items
      return items;
    }

    
    // POUCHDB FUNCTIONS

    sync() {
      //console.log "pouchresourece.sync"
      return window.pouch.sync();
    }

    select(thenCb, catchCb, finallyCb) { // current only
      var that;
      that = this;
      return db.find({
        selector: {
          type: "RootItem",
          is_archived: false
        }
      }).then(function(result) {
        var fixlist;
        fixlist = [];
        //console.log "--- result.docs"
        // console.table result.docs
        result.docs.sortBy('index').forEach(function(doc, index) {
          if (doc.index !== index) {
            // patch the index and update
            doc.index = index;
            return fixlist.append(doc);
          }
        });
        if (fixlist.length > 0) {
          db.bulkDocs(fixlist).then(function(response) {
            console.log("fix index");
            return console.table(fixlist);
          // console.log "reponse"
          // console.log response
          }).catch(function(err) {
            if (typeof errorCallback !== "undefined" && errorCallback !== null) {
              console.log("background reindex error");
              return console.log(err);
            } else {
              console.log("put.catch");
              return console.log(err);
            }
          });
          if (thenCb != null) {
            that.items = that.transformCollection(result.docs);
            return thenCb(that.items);
          }
        } else {
          that.items = that.transformCollection(result.docs);
          return thenCb(that.items);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    selectArchived(thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.find({
        selector: {
          type: "RootItem",
          is_archived: true
        }
      }).then(function(result) {
        if (thenCb != null) {
          that.items = that.transformCollection(result.docs);
          return thenCb(that.items);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    selectFavorites(thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.find({
        selector: {
          type: "RootItem",
          is_favorite: true
        }
      }).then(function(result) {
        if (thenCb != null) {
          that.items = that.transformCollection(result.docs);
          return thenCb(that.items);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    selectOne(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      // console.log "selectone #{id}"
      return db.get(id).then(function(doc) {
        var rootItem;
        //console.log "select one"
        //console.log doc
        if (thenCb != null) {
          rootItem = new RootItem;
          // consider removing transformation
          return thenCb(rootItem.transformModel(doc));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select on e err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    find(options, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.find(options).then(function(result) {
        if (thenCb != null) {
          return thenCb(that.transformCollection(result.docs));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("find  err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    transformBeforeSave(document) {
      // save safe object

      return document;
    }

    insert(obj, thenCb, catchCb, finallyCb) {
      var newRecord, that, writeRecord;
      that = this;
      newRecord = new RootItem(obj);
      // newRecord = rootItem.stub(obj)

      // set ordinal based on list length
      newRecord.index = that.items.length;
      //console.log "newRecord"
      //console.log newRecord
      newRecord.created_at = Date.create();
      //newRecord = @transformBeforeSave(newRecord)

      // todo: guid consistency check first
      console.log("NER RECORD");
      writeRecord = newRecord.pouchify();
      // delete writeRecord.id
      // bdelete writeRecord._id
      writeRecord["_id"] = writeRecord.id;
      console.debug(writeRecord);
      return db.put(writeRecord).then(function(response) {
        // note the response is this format:
        // ok: true
        // id: id
        // rev: rev_id
        if (thenCb != null) {
          return thenCb(response);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("insert.catch");
          return console.log(err);
        }
      }).finally(function() {
        console.log("insert finally");
        that.sync();
        if (finallyCb != null) {
          console.log("insert.finally");
          return finallyCb();
        }
      });
    }

    update(changedDoc, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      console.log("updateDocument");
      console.log(changedDoc);
      //console.log "attempt to update #{changedDoc.id}"
      return db.get(changedDoc.id).then(function(originalDoc) {
        changedDoc["_rev"] = originalDoc._rev;
        changedDoc.updated_at = Date.create();
        changedDoc = that.transformBeforeSave(changedDoc);
        if (thenCb != null) {
          return thenCb(db.put(changedDoc));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("update.catch");
          return console.log(err);
        }
      }).finally(function() {
        console.log("update finally");
        that.sync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    patch(changedDoc, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      console.error("Patch");
      return db.get(changedDoc._id).then(function(originalDoc) {
        Object.merge(originalDoc, changedDoc);
        console.log("changed doc");
        originalDoc.updated_at = Date.create();
        console.log(originalDoc);
        return db.put(originalDoc).then(function(response) {
          if (thenCb != null) {
            return thenCb(that.items);
          }
        // that.select refreshedCb, catchCb, finallyCb
        }).catch(function(err) {
          if (catchCb != null) {
            return catchCb(err);
          } else {
            console.log("put.catch");
            return console.log(err);
          }
        }).finally(function() {
          that.sync();
          if (finallyCb != null) {
            return finallyCb();
          }
        });
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("update.catch");
          return console.log(err);
        }
      }).finally(function() {
        that.sync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    destroy(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      console.error(`Destroy froim list ${id}`);
      return db.find({
        selector: {
          "id": id
        }
      }).then(function(response) {
        var refreshedCb, removedCb;
        if (thenCb != null) {
          refreshedCb = function() { // execute then cb
            return thenCb(that.items);
          };
          removedCb = function() { // refresh @ items
            that.sync();
            return refreshedCb();
          };
          //that.select refreshedCb, catchCb, finallyCb
          console.log("response");
          console.log(response);
          // docToRemove = response.docs.first()
          response.docs.forEach(function(doc) {
            return doc["_deleted"] = true;
          });
          
          // db.remove docToRemove, removedCb
          return db.bulkDocs(response.docs).then(removedCb).catch(function(err) {
            if (catchCb != null) {
              return catchCb(err);
            } else {
              console.error("RootItem.destroy");
              return console.debug(err);
            }
          });
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("destroy.catch");
          return console.log(err);
        }
      }).finally(function() {
        console.log("destroy finally");
        that.sync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

  };

  RootItemList.prototype.name = "Root Item List";

  RootItemList.prototype.type = "RootItem";

  RootItemList.prototype.version = 1;

  RootItemList.prototype.items = [];

  return RootItemList;

}).call(this);

module.exports = RootItemList;
