<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import tippy from 'tippy.js'

  import Menu from './_Lists.svelte'

  export options = undefined
  tippyObject = undefined
  component = undefined


  handleMenuSelection = (selection)->
    dispatch "select", selection
    
  popover = (element, content) ->
    tippyOptions =
      theme: "menu"
      appendTo: 'parent'
      trigger: "click"
      placement: "bottom-end"
      interactive: true
      popperOptions:
        modifiers: [
          {
            name: "flip"
            options:
              fallbackPlacements: ["top","right","left"]
          }

        ]


    if (typeof content == 'function')

      tippyOptions.onCreate = (instance) ->
        component = new content(target: instance.popper.querySelector('.tippy-content'))
        component.$set { options: options }        
        component.$on "selected",  (e) ->
          tippyObject.hide()
          handleMenuSelection e.detail
        return

    else
      tippyOptions.content = content
    if Object.keys(tippyOptions).length == 0
      tippyObject = tippy(element)
    else
      tippyObject = tippy(element, tippyOptions)
    return
    {
      update: (params) ->
        console.error "update"
        #if component
        #  component.$set { value: selectedDate }

        return
      destroy: ->
        tippyObject.destroy()
        if component
          # debugger
          component.$destroy()
        return

    }
  updatedOptions = (val)->
    # console.error "--------------------------------------updatedD"
    if component?
      component.$set {options: val}
      
  `$: updatedOptions(options)`

</script>

<button type="button" icon use:popover={Menu}>
  <slot>
    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M3 17v2h6v-2H3zM3 5v2h10V5H3zm10 16v-2h8v-2h-8v-2h-2v6h2zM7 9v2H3v2h4v2h2V9H7zm14 4v-2H11v2h10zm-6-4h2V7h4V5h-4V3h-2v6z"/></svg>
  </slot>
</button>
